/* Global Styles */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Lato', sans-serif;
  background-color: #f3f8f3; /* Light Green background */
  color: #333;
  line-height: 1.6;
  padding: 0;
  font-size: 16px;
}

h1, h2, h3 {
  color: #2c3e50; /* Darker text color for headings */
  font-weight: 700;
}

/* Link Colors */
a {
  color: #27ae60; /* Green for links */
  text-decoration: none;
  transition: color 0.3s ease;
}

a:hover {
  color: #1f8c4a; /* Darker green for hover effect */
}

/* Navbar Styles */
nav {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #ffffff; /* White background for navbar */
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
  padding: 15px 0;
}

nav ul {
  display: flex;
  justify-content: center;
  list-style-type: none;
}

nav ul li {
  margin: 0 15px;
}

nav ul li a {
  color: #3498db; /* Blue for navbar links */
  font-weight: 600;
  font-size: 18px;
  transition: color 0.3s ease;
}

nav ul li a:hover {
  color: #27ae60; /* Green on hover */
}

/* Buttons */
button {
  padding: 12px 25px;
  background-color: #27ae60; /* Green button */
  color: white;
  border: none;
  border-radius: 6px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

button:hover {
  background-color: #2ecc71; /* Light Green on hover */
}

button:disabled {
  background-color: #ddd;
  cursor: not-allowed;
}

/* Form Styles */
form {
  background-color: #ffffff;
  padding: 35px;
  border-radius: 8px;
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.1);
  width: 100%;
  max-width: 450px;
  margin: 50px auto;
}

form input {
  width: 100%;
  padding: 12px;
  border: 1px solid #ddd;
  border-radius: 6px;
  font-size: 16px;
}

form input:focus {
  border-color: #27ae60; /* Green focus border */
  outline: none;
}

form button {
  width: 100%;
  padding: 14px;
  background-color: #27ae60;
  color: white;
  border: none;
  border-radius: 6px;
  font-size: 16px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

form button:hover {
  background-color: #2ecc71; /* Green button hover */
}

form button:disabled {
  background-color: #ddd;
  cursor: not-allowed;
}

/* About Us Page */
.about-us {
  text-align: center;
  padding: 60px 20px;
  background-color: #ffffff;
}

.about-us h1 {
  font-size: 2.5rem;
  color: #27ae60; /* Green for About Us heading */
  font-weight: 700;
  margin-bottom: 20px;
}

.about-us h2 {
  font-size: 2rem;
  color: #3498db; /* Blue for subheading */
  font-weight: 700;
  margin-top: 40px;
}

.about-us p {
  font-size: 1.125rem;
  color: #555;
  line-height: 1.8;
  margin-bottom: 30px;
}

.about-us button {
  background-color: #3498db; /* Blue button */
  color: white;
  padding: 12px 24px;
  border: none;
  border-radius: 6px;
  cursor: pointer;
  font-size: 16px;
  transition: background-color 0.3s ease;
}

.about-us button:hover {
  background-color: #2980b9; /* Darker blue on hover */
}

/* Team Section */
.team {
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
  gap: 20px;
  margin-top: 40px;
}

.team-member {
  width: 200px;
  text-align: center;
  padding: 20px;
  background-color: #ffffff; /* White background */
  border-radius: 8px;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
}

.team-member img {
  width: 120px;
  height: 120px;
  border-radius: 50%;
  margin-bottom: 15px;
}

.team-member h3 {
  font-size: 1.2rem;
  font-weight: 700;
  color: #2c3e50;
}

.team-member p {
  font-size: 0.9rem;
  color: #555;
}

/* LinkedIn Icon */
.linkedin-logo {
  width: 20px; /* Adjust size */
  height: 20px;
  margin-top: 10px;
  transition: opacity 0.3s ease;
}

.linkedin-logo:hover {
  opacity: 0.7; /* Subtle effect on hover */
}

/* Responsive Design */
@media (max-width: 768px) {
  nav {
    flex-direction: column;
    padding: 20px 15px;
  }

  nav ul {
    flex-direction: column;
    align-items: center;
    margin-top: 20px;
  }

  .team {
    flex-direction: column;
    align-items: center;
  }

  .team-member {
    width: 80%;
    max-width: 300px;
    margin-bottom: 30px;
  }
}
